import { Component } from '@angular/core';

  
import { IonicPage } from 'ionic-angular';
import { sendUsersService } from '../../services/sendUsers.service';
import { AngularFireAuth } from 'angularfire2/auth';
import { SignUpService } from '../../services/signup.service';
@IonicPage()
@Component({
  templateUrl: 'tabs.html'
})
export class TabsPage {

  tab1Root = 'FindridePage';
  tab2Root = 'MyridePage';
  tab4Root = 'WalletPage';
  tab5Root = 'MorePage';
  driverUid;
  user;
  badge = '0';
  constructor(public signUpService: SignUpService, public angularFireAuth: AngularFireAuth) {
    this.driverUid = this.angularFireAuth.auth.currentUser.uid;

    this.signUpService.getMyInfo(this.driverUid).subscribe(user=>{
      this.user = user;

      if(this.user.badge == true){
        this.badge = '1';
      }else if(this.user.badge == false){
        this.badge = '0';
      }
    })
  }
}



